@import "./../../utils/constants";

.skills-card {
  display: flex;
  flex-direction: row;
}

.skills-icons {
  list-style: none;
  text-align: center;
}

.skills-inline {
  display: inline-block;
  margin-right: 50px;
}

.skills-inline > img {
  margin: 0 1rem 0 2rem;
  object-fit: contain;
  width: 5rem;
  height: 5rem;
}

.skills-inline > p {
  text-align: center;
}
